
$메인칼라 : #ff0000;


.red {
    color:$메인칼라;
}
// 함수처럼 사용가능
@mixin fn_my-alert(){
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
    p{
        margin-bottom: 0;
    }
}

.my-alert{
    @include fn_my-alert()
}

.my-alert-red {
    @extend .my-alert;
    background-color:red;
}

.my-alert-pink {
    @extend .my-alert;
    background-color:hotpink;
}



