.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.jumbotron {
  padding: 4rem 2rem;
  margin-bottom: 2rem;
  background-color: var(--bs-light);
  border-radius: .3rem;  
}
.jumbotron-background{
  /*public에 있는 이미지 파일은 아래와 같이 절대 경로를 사용해야함*/
  background-image: url('D:\리액트강의\React class\shop\public\bg.jpg');
  /*src에 있는 이미지 파일은 아래와 같이 걍 사용하면댐*/
  background-image: url(./bg.jpg);
  background-size: cover;
  color:white;
  border: 1px solid black;
}

.linkStyle{
  display: block;
    padding: .5rem 1rem;
    color: #282c34;
    text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
